@import "../../../../style/variables.scss";

.cities__wrapper {
    height: 82.5vh;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 15vh;
    grid-auto-rows: 15vh;
    row-gap: 2.8vh;
    margin-top: 3vh;
    overflow-y:auto;
}

.empty {
    margin: 30px auto;
    text-align: center;
    font-size: 20px;
    color: $grey;

    span {
        margin-left: 5px;
    }
}
